<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-lx-calendar"></i> 网站授权管理</el-breadcrumb-item>
                <el-breadcrumb-item>网站信息批量上传</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="content-title">批量导入</div>
            <div class="plugins-tips">
            </div>
            <div>
                <el-upload
                        class="upload-demo"
                        :action="posterUrl"
                        :http-request="uploadImage"
                        :on-preview="handlePreview"
                        :on-remove="handleRemove"
                        :before-remove="beforeRemove"
                        :before-upload="beforeUpload"
                        multiple
                        :on-exceed="handleExceed"
                        :file-list="fileList">
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                </el-upload>
                <!--                <br/>-->
                <!--                <el-button size="small" type="primary" @click="uploadFile">立即上传</el-button>-->
                <!--                <el-button size="small">取消</el-button>-->
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'
    export default {
        name: 'websiteUpload',
        data: function(){
            return {
                posterUrl: '',
                limitNum: 1,  // 上传excell时，同时允许上传的最大数
                fileList: [],   // excel文件列表
                headers:{Authorization:localStorage.getItem('agent')}
            }
        },
        computed: {
            ...mapState(['userInfo']),
        },
        methods:{

            uploadImage(file) {
                console.log(file);
                let uploadData = new FormData();
                uploadData.append('file', file.file);
                uploadData.append('username', this.userInfo.username);
                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                        'Authorization':localStorage.getItem('agent')
                    }
                };
                this.$confirm('确定要上传吗？', '提示', {
                    type: 'warning'
                }).then(() => {
                    this.$axios.post("http://localhost:3000/api/websiteUpload/websiteUpload",uploadData, config)
                        .then(res=>{
                            console.log(res.data)

                            if(res.data.statusCode === 200) {
                                this.$message.success(res.data.message)
                                // file.onSuccess();
                                // this.imgUrls.push({name:params.file.name, url:res.data.url});
                                // console.log(this.imgUrls);
                            }else{
                                this.$message.error('上传操作失败');
                            }
                        }).catch(error=>{
                        file.onError();
                        this.$message.error('上传失败');
                    });
                }).catch(() => {
                    this.$message.error('已取消上传');
                });
            },

            // uploadPoster(url, obj, config) {
            //     let poster_upload_path = "http://localhost:3000/api/tableUpload/tableUpload";
            //     return this.$axios.post(poster_upload_path, obj, config);
            // },


            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePreview(file) {
                console.log(file);
            },
            handleExceed(files, fileList) {
                this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },
            beforeRemove(file, fileList) {
                return this.$confirm(`确定移除 ${ file.name }？`);
            },



        }
    }
</script>

<style scoped>
    .content-title{
        font-weight: 400;
        line-height: 50px;
        margin: 10px 0;
        font-size: 22px;
        color: #1f2f3d;
    }

</style>
